@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

* {
   padding: 0;
   margin: 0;
   border: 0;
}
*,
*:before,
*:after {
   -moz-box-sizing: border-box;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}
:focus,
:active {
   outline: none;
}
a:focus,
a:active {
   outline: none;
}

nav,
footer,
header,
aside {
   display: block;
}

html,
body {
   height: 100%;
   width: 100%;
   font-style: 100%;
   line-height: 1;
   font-style: 14px;
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
   font-family: inherit;
}

input::-ms-clear {
   display: none;
}
button {
   cursor: pointer;
}
button::-moz-focus-inner {
   padding: 0;
   border: 0;
}
a,
a:visited {
   text-decoration: none;
}
a:hover {
   text-decoration: none;
}
ul li {
   list-style-type: none;
}
img {
   vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   font-style: inherit;
   font-weight: 400;
}

body {
   font-family: var(--font-fm);
   font-weight: var(--font-r);
}

:root {
   /* Colors */
   --bg-color: #eee;
   --body-color: #fff;
   --text-color: black;
   --accent-color: lightgray;
   --main-color: greenyellow;
   --alert-color: red;

   /* Typography */
   --font-fm: 'Poppins', sans-serif;
   --font-r: 400;
   --font-sb: 800;
}
